<template>
  <div id="backstage">
    <Layout>
            <Sider hide-trigger width="208">
              <div class="left_backstage_box">
                <img class="logo" src="/static/icon/logo.png" style="display:block">
                <div class="backstage_option" v-for="(item,index) in menu" :key="index" :class="{'backstage_option_zhan' : menuindex == index }" :style="{'max-height' : menuindex == index ? '500px' : ''}" @click.stop="titlego(index)">
                    <div class="type_title">
                      <img class="icon" :src="item.icon_active" v-if="menuindex == index">
                      <img class="icon" :src="item.icon" v-else>
                      {{item.title}}<span></span>
                      <img class="jiant" src="/static/baseicon/ac.png">
                    </div>
                    <div class="type_dang" v-for="(items,indexs) in item.data"  :key="indexs" @click.stop="danggo(index,items.name)" :style="{'color': menuindex == index && item.active_index == indexs  ? 'rgb(56, 108, 250)' :'','font-weight': menuindex == index && item.active_index == indexs ? 'bold' : ''}">
                    {{items.title}}
                    </div>
                </div>
              </div>
            </Sider>
            <Layout>
              <div class="hide">
                  <div class="hide_z">
                  <div class="coz_box">
                      <div class="bianjie" style="margin-right: 25px;" >
                        <img src="/static/baseicon/7.png" class="img_2">
                        <img src="/static/baseicon/7-s.png" class="img_1">
                        <span>联系客服</span>
                      </div>
                      <div class="bianjie" style="margin-right: 25px;">
                        <img src="/static/baseicon/8.png" class="img_2">
                        <img src="/static/baseicon/8-s.png" class="img_1">
                        <span>意见反馈</span>
                      </div>
                      <div class="bianjie">
                        <img src="/static/baseicon/9.png" class="img_2">
                        <img src="/static/baseicon/9-s.png" class="img_1">
                        <span>便捷操作</span>
                      </div>
                      <div class="bianjie_box">
                        <div @click.stop="$router.push({path:'/capital_change'})"><img src="/static/baseicon/3.png"><img src="/static/baseicon/3-s.png"><span>资金变动</span></div>
                        <div @click.stop="$router.push({path:'/withdraw'})"><img src="/static/baseicon/13.png"><img src="/static/baseicon/13-s.png"><span>申请提现</span></div>
                        <div @click.stop="$router.push({path:'/order_record'})"><img src="/static/baseicon/14.png"><img src="/static/baseicon/14-s.png"><span>交易记录</span></div>
                      </div>
                      <div class="lingd">
                        <img src="/static/baseicon/10.png">
                      </div>
                      <div class="urel_box">
                        <img src="/static/baseicon/11.png">
                        <span v-if="user.phone.length > 11">{{user.phone.slice(0,10)+'...'}}</span>
                        <span v-else>{{user.phone}}</span>
                      </div>
                      <div class="bianjie_box dengc">
                        <div @click.stop="$router.push({path:'/change_password'})"><img src="/static/baseicon/15.png"><img src="/static/baseicon/15-s.png"><span>修改密码</span></div>
                        <div @click.stop="signout"><img src="/static/baseicon/16.png"><img src="/static/baseicon/16-s.png"><span>退出登录</span></div>
                      </div>
                      </div>
                  </div>
              </div>
              <router-view></router-view>
            </Layout>
    </Layout>
  </div>
</template>
<script>
  export default{
    data(){
      return{
        user:{
          phone:''
        },
        menu:[
          {
            title:"我的账户",
            icon:"/static/baseicon/1.png",
            icon_active:"/static/baseicon/1-s.png",
            active_index:0,
            data:[
              {
                title:"信息总汇",
                name:"/main"
              },
              {
                title:"个人信息",
                name:"/user_info"
              },
              {
                title:"登录日志",
                name:"/loginlog"
              },
              {
                title:"资金变动",
                name:"/capital_change"
              },
              {
                title:"支付通道",
                name:"/payment"
              },
              {
                title:"申请提现",
                name:"/withdraw"
              }
            ]
          },
          {
            title:"提款管理",
            icon:"/static/baseicon/2.png",
            icon_active:"/static/baseicon/2-s.png",
            active_index:99,
            data:[
              {
                title:"提款记录",
                name:"/settlement_record"
              },
              {
                title:"提款账户",
                name:"/settlement_account"
              }
            ]
          },
          {
            title:"交易数据",
            icon:"/static/baseicon/3.png",
            icon_active:"/static/baseicon/3-s.png",
            active_index:99,
            data:[
              {
                title:"交易记录",
                name:"/order_record"
              }
            ]
          },
          {
            title:"数据统计",
            icon:"/static/baseicon/4.png",
            icon_active:'/static/baseicon/4-s.png',
            active_index:99,
            data:[
              {
                title:"订单统计",
                name:"/statistics"
              }
            ]
          },
          {
            title:"代理分销",
            icon:"/static/baseicon/17.png",
            icon_active:"/static/baseicon/17-s.png",
            active_index:99,
            data:[
              {
                title:"我的代理",
                name:"/my_agent"
              },
              {
                title:"分润统计",
                name:"/profit_statistics"
              }
            ]
          },
          {
            title:"开发详情",
            icon:"/static/baseicon/6.png",
            icon_active:"/static/baseicon/6-s.png",
            active_index:99,
            data:[
              {
                title:"开发文档",
                name:"/development_file"
              },
              {
                title:"SDK下载",
                name:"/download_sdk"
              }
            ]
          }
        ],
        menuindex:0
      }
    },
    watch:{
      $route(to,from){
          for(var i =0;i<this.menu.length;i++){
              for(var n=0;n<this.menu[i].data.length;n++){
                  if(this.menu[i].data[n].name == to.path){
                      this.menuindex = i;
                      this.menu[i].active_index = n;
                      break;
                  }else{
                    this.menu[i].active_index = 99;
                  }
              }
          }
       }
    },
    mounted(){
      this.getuserinfo();
      for(var i =0;i<this.menu.length;i++){
          for(var n=0;n<this.menu[i].data.length;n++){
              if(this.menu[i].data[n].name == this.$route.path){
                  this.menuindex = i;
                  this.menu[i].active_index = n;
                  break;
              }
          }
      }
    },
    methods:{
      signout(){
        window.localStorage.removeItem('user_token');
        window.localStorage.removeItem('user_info');
        this.$router.push({path:"/login"});
      },
      titlego(i){
        if(this.menuindex == i){
            this.menuindex = 99;
        }else{
            this.menuindex = i;
        }
      },
      danggo(i,path){
        if(path != this.$route.path){
          this.$router.push({path:path});
        }
      },
	  getuserinfo(){
		  if(localStorage.getItem('user_info')){
        var user_info = JSON.parse(localStorage.getItem('user_info'));
        if(user_info.time < new Date().getTime()){
          this.ajaxuserinfo();
        }else{
          this.user = user_info.data;
        }
		  }else{
        this.ajaxuserinfo();
      }
	  },
    ajaxuserinfo(){
      this.$http.get('api/user/userinfo',{}, res => {
           if (res.status == 200) {
               if(res.data.code == 200){
                 var userinfo = {};
                     userinfo.time = new Date().getTime() + 10000;
                     userinfo.data = res.data.data;
                 localStorage.setItem('user_info',JSON.stringify(userinfo));
                 this.user = userinfo.data;
               }else if(res.data.code == 201){
                 this.$Message.warning(res.data.msg);
               }
           } else {
              console.log(res.message);
           }
      });
    }
    }
  }
</script>



